import {Component, OnInit, ViewEncapsulation} from "@angular/core";
import {Router, RouteParams} from "@angular/router-deprecated";
import { Observable } from 'rxjs/Observable';
import {Account} from '../account/account.model';
import { Doctor } from './doctor.model';
import { DoctorService } from './doctor.service';
import { doctors as dumpDoctors} from '../shared/dump.model';
@Component({
    selector: 'admin-patient-details',
    template: require('./doctor-details.component.html'),
    styles: [
        require('../../sass/layouts/doctor-details-content.scss')
    ],
    providers: [DoctorService]
})
export class DoctorDetailsComponent implements OnInit {
    private doctor: Doctor = new Doctor();
    private account: Account;

    private userDataLoading = false;
    private btnStatusDataLoading = false;

    constructor(private router: Router,
        private routeParams: RouteParams,
        private doctorService: DoctorService) { }

    ngOnInit() {
        let id = this.routeParams.get('id');

        //this.doctor = dumpDoctors[1];

        // check account
        this.account = JSON.parse(window.sessionStorage.getItem('account'));
        if (!this.account) {
            window.location.href = "/signin";
        }
        this.userDataLoading = true;
        this.doctorService.getDoctor(this.account, id)
            .subscribe(
            success => {
                if (success.status == 1) {
                    this.doctor = success.data;
                } else {
                    console.log(success.errorMsg);
                }
                this.userDataLoading = false;
            },
            error => {
                console.error("rERROR:" + error);
                this.userDataLoading = false;
            }
            );
    }

    // Show status seleciton dialog
    showStatusSelectionModal() {
        let modalElem = <HTMLDivElement>document.getElementById('modal-change-status');
        if (!modalElem.classList.contains('modal-open')) {
            modalElem.classList.add('modal-open');
            modalElem.classList.remove('modal-close');
        }

    }

    hideStatusSelectionModal() {
        let modalElem = <HTMLDivElement>document.getElementById('modal-change-status');
        if (modalElem.classList.contains('modal-open')) {
            modalElem.classList.remove('modal-open');
            modalElem.classList.add('modal-close');
        }
    }

    saveStatusSelection() {
        let status: number = this.doctor.status;
        let deactiveRadioElem = <HTMLInputElement>document.getElementById('deactive-radio');
        let pendingRadioElem = <HTMLInputElement>document.getElementById('pending-radio');
        let activeRadioElem = <HTMLInputElement>document.getElementById('active-radio');
        if (deactiveRadioElem.checked) {
            status = parseInt(deactiveRadioElem.value);
        } else if (pendingRadioElem.checked) {
            status = parseInt(pendingRadioElem.value);
        } else if (activeRadioElem.checked) {
            status = parseInt(activeRadioElem.value);
        }

        this.hideStatusSelectionModal();
        this.btnStatusDataLoading = true;
        if ((status == 0 || status == 1 || status == 2) && this.doctor.status != status) {
            this.doctorService.changeStatus(this.account, this.doctor.id, status)
                .subscribe(
                success => {
                    if (success.status == 1) {
                        this.doctor.status = status;
                    } else  {
                        console.log(success.errorMsg);
                    }
                    this.btnStatusDataLoading = false;
                },
                error => {
                    console.error('rERROR' + error);
                    this.btnStatusDataLoading = false;
                }
                );
        }
    }

    cancelStatusSelectionModal() {
        this.hideStatusSelectionModal();
    }

    diagnostic() {
        return JSON.stringify(this.doctor);
    }

}


