	module.exports = "\r\n<article id=\"doctor-details\">\r\n  <div class=\"spinner--dot\" *ngIf=\"userDataLoading\">\r\n      <div class=\"bounce1\"></div>\r\n      <div class=\"bounce2\"></div>\r\n      <div class=\"bounce3\"></div>\r\n  </div>\r\n  <div id=\"main-info\" *ngIf=\"!userDataLoading\">\r\n    <div id=\"photo\">\r\n      <img src=\"https://unsplash.it/g/200\" alt=\"photo\">\r\n    </div>\r\n    <div id=\"head-info\">\r\n      <div id=\"div-name-status\">\r\n        <h2 id=\"name-item\">{{doctor.firstName + ' ' + doctor.lastName}}</h2>\r\n        <div class=\"div-status\" id=\"doctor-status\">\r\n          <button id=\"btnt-rigger-dialog\" (click)=\"showStatusSelectionModal()\" class=\"\" [class.btn-deactive]=\"doctor.status == 0\"\r\n          [class.btn-pending]=\"doctor.status == 1\" [class.btn-active]=\"doctor.status == 2\"><span *ngIf=\"btnStatusDataLoading\" class=\"spinner--circle  spinner--circle--small\">Loading</span>{{doctor.status == 0 ? 'Deactive' : doctor.status == 1 ? 'Pending' : 'Active'}}</button>\r\n          <div id=\"modal-change-status\" class=\"modal\">\r\n            <div class=\"modal-overlay\"></div>\r\n            <div class=\"modal-content\">\r\n              <div class=\"modal-content-header\">\r\n                <h3 class=\"modal-title\">Change status of doctor <strong>{{doctor.firstName + ' ' + doctor.lastName}}</strong> ?</h3>\r\n                <button class=\"btn-cancel-trans close-action\" id=\"action-close\" (click)=\"cancelStatusSelectionModal()\">\r\n                  <svg class=\"svg-icon\" viewBox=\"0 0 20 20\">\r\n                    <path fill=\"none\" d=\"M15.898,4.045c-0.271-0.272-0.713-0.272-0.986,0l-4.71,4.711L5.493,4.045c-0.272-0.272-0.714-0.272-0.986,0s-0.272,0.714,0,0.986l4.709,4.711l-4.71,4.711c-0.272,0.271-0.272,0.713,0,0.986c0.136,0.136,0.314,0.203,0.492,0.203c0.179,0,0.357-0.067,0.493-0.203l4.711-4.711l4.71,4.711c0.137,0.136,0.314,0.203,0.494,0.203c0.178,0,0.355-0.067,0.492-0.203c0.273-0.273,0.273-0.715,0-0.986l-4.711-4.711l4.711-4.711C16.172,4.759,16.172,4.317,15.898,4.045z\"></path>\r\n                  </svg>\r\n                </button>\r\n              </div>\r\n              <div class=\"modal-content-main\">\r\n                <div class=\"form-radio\">\r\n                  <div class=\"radio\">\r\n                    <label>\r\n                    <input type=\"radio\" id=\"deactive-radio\" name=\"status\" value=\"0\" [attr.checked]=\"doctor.status == 0 ? true:null\">\r\n                    <i class=\"helper\"></i>Deactive\r\n                    </label>\r\n                  </div>\r\n                  <div class=\"radio\">\r\n                    <label>\r\n                    <input type=\"radio\" id=\"pending-radio\"name=\"status\" value=\"1\" [attr.checked]=\"doctor.status == 1 ? true:null\">\r\n                    <i class=\"helper\"></i>Pending\r\n                    </label>\r\n                  </div>\r\n                  <div class=\"radio\">\r\n                    <label>\r\n                    <input type=\"radio\" id=\"active-radio\" name=\"status\" value=\"2\" [attr.checked]=\"doctor.status == 2 ? true:null\">\r\n                    <i class=\"helper\"></i>Active\r\n                    </label>\r\n                  </div>\r\n                </div>\r\n              </div>\r\n              <div class=\"modal-content-actions\">\r\n                <button class=\"btn-ok btn-action\" id=\"action-open\" (click)=\"saveStatusSelection()\">Save</button>\r\n              </div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n      </div>\r\n      <span id=\"email-item\">{{doctor.email}}</span>\r\n      <div id=\"budget-item\"><span class=\"budget-value\">{{doctor.money}}</span><span class=\"currency\">vnd</span></div>\r\n    </div>\r\n  </div>\r\n  <div id=\"additional-info\" *ngIf=\"!userDataLoading\">\r\n    <div class=\"info-item\">\r\n      <span class=\"key\">Birthday</span>\r\n      <span class=\"value\">{{doctor.birthday ? (doctor.birthday | date: 'longDate') : '' }}</span>\r\n    </div>\r\n    <div class=\"info-item\">\r\n      <span class=\"key\">Gender</span>\r\n      <span class=\"value\">{{doctor.gender == 0 ? 'Male' : 'Female'}}</span>\r\n    </div>\r\n    <div class=\"info-item\">\r\n      <span class=\"key\">Phone number</span>\r\n      <span class=\"value\">{{doctor.phone}}</span>\r\n    </div>\r\n    <div class=\"info-item\">\r\n      <span class=\"key\">Address</span>\r\n      <span class=\"value\">{{doctor.address}}</span>\r\n    </div>\r\n    <div class=\"info-item\">\r\n      <span class=\"key\">Rank</span>\r\n      <span class=\"value\">{{doctor.rank}}</span>\r\n    </div>\r\n    <div class=\"info-item\">\r\n      <span class=\"key\">Specialty</span>\r\n      <span class=\"value\">{{doctor.speciality}}</span>\r\n    </div>\r\n    <div class=\"info-item\">\r\n      <span class=\"key\">Joined at</span>\r\n      <span class=\"value\">{{doctor.created ? ( doctor.created | date: 'longDate') : ''}}</span>\r\n    </div>\r\n  </div>\r\n</article>";

