import { Injectable } from "@angular/core";
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Doctor, StatusStatistic} from "./doctor.model";
import { SingleOutputModel, ArrayOutputModel } from "../shared/output.model";
import { Observable } from 'rxjs/Observable';
import { Account } from '../account/account.model';

@Injectable()
export class DoctorService {
    private URL_GET_DOCTORS = '/api/doctor/filter';
    private URL_GET_DOCTOR = 'api/doctor';
    private URL_CHANGE_STATUS_DOCTOR = '/api/person/status';
    private URL_STATISTIC_DOCTOR = '/api/person/statistic/status';

    constructor(private http: Http) { }

    getDoctor(account: Account, id: string) {
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        });
        let options = new RequestOptions({ headers: headers });
        return this.http.get(this.URL_GET_DOCTOR + `?id=${id}`, options)
            .map(this.extractDataGet)
            .catch(this.handlerError);
    }

    filterDoctors(account: Account, email: string, status: string, pageNo: number) {
        // In server: page start from 0, in client, page start from 1
        pageNo = pageNo - 1;
        let data = {
            page: pageNo
        }
        if (status && status != '-1') {
            data['Status'] = status;
        }
        if (email) {
            data['Email'] = email;
        }
        let body = JSON.stringify(data);
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        }
        );
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.URL_GET_DOCTORS, body, options)
            .map(this.extractDataFilter)
            .catch(this.handlerError);
    }

    changeStatus(account: Account, id: string, status: number) {
        let body = JSON.stringify({
            Id: id,
            Status: status
        });
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        }
        );
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.URL_CHANGE_STATUS_DOCTOR, body, options)
            .map(this.extractDataChangeStatus)
            .catch(this.handlerError);
    }

    getDoctorStatusStatistic(account: Account) {
        let body = JSON.stringify({
            Role: 3
        });
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        }
        );
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.URL_STATISTIC_DOCTOR, body, options)
            .map(this.extractDataStatusStatistic)
            .catch(this.handlerError);
    }

    private extractDataFilter(res: Response) {
        if (res.status == 200) {
            let data = res.json().Users;
            let doctors: Doctor[] = [];
            for (let item of data) {
                let doctor = new Doctor();
                doctor.id = item.Id;
                doctor.firstName = item.FirstName;
                doctor.lastName = item.LastName;
                doctor.email = item.Email;
                doctor.birthday = new Date(item.Birthday);
                doctor.gender = item.Gender;
                doctor.address = item.Address;
                doctor.long = item.Longitude;
                doctor.lat = item.Latitude;
                doctor.phone = item.Phone;
                doctor.status = item.Status;
                doctor.photo = item.Photo;
                doctor.money = item.Money;
                doctor.rank = item.Rank;
                doctor.speciality = item.Speciality;
                doctor.created = item.Created;
                doctor.lastModified = item.LastModified;

                doctors.push(doctor);
            }

            let doctorsOutModel = new ArrayOutputModel(1, doctors, null);
            doctorsOutModel.total = parseInt(res.json().Total);
            return doctorsOutModel;
        } 

        if (res.status == 400 || res.status == 401 || res.status == 403) {
            return new ArrayOutputModel(0, null, res.json().Errors);
        }
    }

    private extractDataGet(res: Response) {
        if (res.status == 200) {
            let data = res.json().User;
            let doctor = new Doctor();
            doctor.id = data.Id;
            doctor.firstName = data.FirstName;
            doctor.lastName = data.LastName;
            doctor.email = data.Email;
            //doctor.birthday = new Date(data.Birthday);
            doctor.gender = data.Gender;
            doctor.address = data.Address;
            doctor.long = data.Longitude;
            doctor.lat = data.Latitude;
            doctor.phone = data.Phone;
            doctor.status = data.Status;
            doctor.photo = data.Photo;
            doctor.money = data.Money;
            doctor.rank = data.Rank;
            doctor.speciality = data.Speciality;
            //doctor.created = data.Created;
            //doctor.lastModified = data.LastModified;

            return new SingleOutputModel(1, doctor, null);
        }

        if (res.status == 400 || res.status == 401 || res.status == 403) {
            return new SingleOutputModel(0, null, res.json().Errors);
        }
    }

    private extractDataStatusStatistic(res: Response) {
        if (res.status == 200) {
        let data = res.json();
        let statusStatistic: StatusStatistic = new StatusStatistic();
        statusStatistic.activeAccounts = data.ActiveAccounts;
        statusStatistic.pendingAccounts = data.PendingAccounts;
        statusStatistic.deactiveAccounts = data.DeactiveAccounts;
        statusStatistic.totalAccounts = data.Total;

            return new SingleOutputModel(1, statusStatistic, null);
        } 

        if (res.status == 401 || res.status == 403) {
            return new SingleOutputModel(0, null, res.json().Errors);
        }
    }

    private extractDataChangeStatus(res: Response) {
        if (res.status = 200) {
            return new SingleOutputModel(1, true, null);
        } else {
            return new SingleOutputModel(0, false, null);
        }
    }

    private handlerError(error: any) {
        let errMsg: string;
        if (error.message) {
            errMsg = error.message;
        } else if (error.status) {
            errMsg = `${error.status} - ${error.statusText}`;
        } else {
            errMsg = 'Server error';
        }

        console.log(errMsg);
        return Observable.throw(errMsg);
    }
}





