import {Injectable} from "@angular/core";
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Patient, StatusStatistic } from "./patient.model";
import Location from "../shared/location";
import Country from "../shared/country";
import { Observable } from 'rxjs/Observable';
import {Account} from '../account/account.model';
import { SingleOutputModel, ArrayOutputModel } from '../shared/output.model';

@Injectable()
export default class PatientService {
    private URL_GET_PATIENTS = '/api/patient/filter';
    private URL_GET_PATIENT = '/api/patient';
    private URL_CHANGE_STATUS_PATIENT = '/api/person/status';
    private URL_STATISTIC_PATIENT = '/api/person/statistic/status';

    constructor(private http: Http) { }

    getPatient(account: Account, id: string) {
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        }
        );
        let options = new RequestOptions({ headers: headers });
        return this.http.get(this.URL_GET_PATIENT + `?id=${id}`, options)
            .map(this.extractDataGet)
            .catch(this.handlerError);
    }

    filterPatients(account: Account, email: string, status: string, pageNo: number, records: number) {
        // In server: page start from 0, in client, page start from 1
        pageNo = pageNo - 1;

        let data = {
            page: pageNo,
            records: records
        }
        if (status && status != '-1') {
            data['Status'] = status;
        }
        if (email) {
            data['Email'] = email;
        }
        let body = JSON.stringify(data);
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        }
        );
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.URL_GET_PATIENTS, body, options)
            .map(this.extractDataFilter)
            .catch(this.handlerError);
    }

    getDoctorStatusStatistic(account: Account) {
        let body = JSON.stringify({
            Role: 2
        });
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        }
        );
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.URL_STATISTIC_PATIENT, body, options)
            .map(this.extractDataStatusStatistic)
            .catch(this.handlerError);
    }

    changeStatus(account: Account, id: string, status: number) {
        let body = JSON.stringify({
            Id: id,
            Status: status
        });
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        }
        );
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.URL_CHANGE_STATUS_PATIENT, body, options)
            .map(this.extractDataChangeStatus)
            .catch(this.handlerError);
    }

    private extractDataFilter(res: Response) {
        if (res.status == 200) {
            let data = res.json().Users;
            let patients: Patient[] = [];
            for (let item of data) {
                let patient = new Patient();
                patient.id = item.Id;
                patient.firstName = item.FirstName;
                patient.lastName = item.LastName;
                patient.email = item.Email;
                patient.birthday = new Date(item.Birthday);
                patient.gender = item.Gender;
                patient.address = item.Address;
                patient.long = item.Longitude;
                patient.lat = item.Latitude;
                patient.phone = item.Phone;
                patient.status = item.Status;
                patient.photo = item.Photo;
                patient.money = item.Money;
                patient.created = item.Created;
                patient.lastModified = item.LastModified;

                patients.push(patient);
            }

            let outputModel = new ArrayOutputModel(1, patients, null);
            outputModel.total = res.json().Total;
            return outputModel;
        }

        if (res.status == 400 || res.status == 401 || res.status == 403) {
            return new ArrayOutputModel(0, null, res.json().Errors);
        }
    }

    private extractDataGet(res: Response) {
        if (res.status == 200) {
            let data = res.json().User;
            let patient = new Patient();
            patient.id = data.Id;
            patient.firstName = data.FirstName;
            patient.lastName = data.LastName;
            patient.email = data.Email;
            //patient.birthday = new Date(data.Birthday);
            patient.gender = data.Gender;
            patient.address = data.Address;
            patient.long = data.Longitude;
            patient.lat = data.Latitude;
            patient.phone = data.Phone;
            patient.status = data.Status;
            patient.photo = data.Photo;
            patient.money = data.Money;
            //patient.created = data.Created;
            //patient.lastModified = data.LastModified;
            return new SingleOutputModel(1, patient, null);
        }
        if (res.status == 400 || res.status == 401 || res.status == 403 || res.status == 404) {
            return new SingleOutputModel(0, null, res.json().Errors);
        }
    }

    private extractDataStatusStatistic(res: Response) {
        if (res.status == 200) {
            let data = res.json();
            let statusStatistic: StatusStatistic = new StatusStatistic();
            statusStatistic.activeAccounts = data.ActiveAccounts;
            statusStatistic.deactiveAccounts = data.DeactiveAccounts;
            statusStatistic.totalAccounts = data.Total;

            return new SingleOutputModel(1, statusStatistic, null);
        }
        if (res.status == 401 || res.status == 403) {
            return new SingleOutputModel(0, null, res.json().Errors);
        }
    }

    private extractDataChangeStatus(res: Response) {
        if (res.status = 200) {
            return new SingleOutputModel(1, true, null);
        } else {
            return new SingleOutputModel(0, false, res.json().Errors);
        }
    }

    private handlerError(error: any) {
        let errMsg: string;
        if (error.message) {
            errMsg = error.message;
        } else if (error.status) {
            errMsg = `${error.status} - ${error.statusText}`;
        } else {
            errMsg = 'Server error';
        }

        console.log(errMsg);
        return Observable.throw(errMsg);
    }
}





