import { Injectable } from '@angular/core';

@Injectable()
export default class Logger {
    logs: string[] = [];
    
    constructor(private className: string) {
        
    }
    log(msg: any) {
        this.logs.push(msg);
        console.log('[' + this.className + '] ' + msg);
    }      
    
    error(msg: any) {
        console.error('[' + this.className + '] ' + msg);
    }
    
    warn(msg: any) {
        console.warn('[' + this.className + '] ' + msg);
    }
}


