export class SingleOutputModel {
    constructor(public status: number = 0,
        public data: any,
        public errorMsg: Array<string>) { }
}

export class ArrayOutputModel {
    total: number;
    constructor(public status: number = 0,
        public data: any[],
        public errorMsg: Array<string>) { }
}


