import {Injectable} from "@angular/core";
import {Http, Response, Headers, RequestOptions} from '@angular/http';
import {Account} from "./account.model";
import {SingleOutputModel} from "../shared/output.model";
import {SigninModel} from "./signin.model";
import {Observable} from 'rxjs/Observable';
import Logger from "../shared/logger.service";
import * as moment from "moment";
import * as lodash from 'lodash';

@Injectable()
export default class SigninService {
    private SIGN_IN_URL = "/api/admin/login";

    constructor(private http: Http) { }

    signIn(user: SigninModel) {
        let body = JSON.stringify({
            Email: user.email,
            Password: user.password
        });
        console.log(body);
        let headers = new Headers({
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        });
        let options = new RequestOptions({ headers: headers });

        return this.http.post(this.SIGN_IN_URL, body, options)
            .map(this.extractSigninData)
            .catch(this.handlerSigninError);
    }

    private extractSigninData(res: Response) {
        if (res.status == 200) {
            let data = res.json().User;

            let account: Account = new Account();
            account.id = data.Id;
            account.firstName = data.FirstName;
            account.lastName = data.LastName;
            account.email = data.Email;
            account.password = data.Password;
            account.birthday = new Date(data.Birthday);
            account.gender = data.Gender;
            account.address = data.Address;
            account.long = data.Longitude;
            account.lat = data.Latitude;
            account.phone = data.Phone;
            account.role = data.Role;
            account.status = data.Status;
            account.photo = data.Photo;
            account.created = new Date(data.Created);
            account.lastModified = new Date(data.LastModified);

            return new SingleOutputModel(1, account, null);
        } 

        if (res.status == 404 || res.status == 400) {
            return new SingleOutputModel(0, null, res.json().Errors);
        }
    }

    private handlerSigninError(error: any) {
        let errMsg: string;
        if (error.message) {
            errMsg = error.message;
        } else if (error.status) {
            errMsg = `${error.status} - ${error.statusText}`;
        } else {
            errMsg = 'Server error';
        }

        console.log(errMsg);
        return Observable.throw(errMsg);
    }
}




