export class Patient{
    id: string;
    firstName: string = '';
    lastName: string = '';
    email: string = '';
    password: string;
    birthday: Date;
    gender: number;
    phone: string;
    role: number;
    status: number;
    address: string;
    long: number;
    lat: number;
    photo: string = 'https://unsplash.it/g/200';
    created: Date;
    lastModified: Date;
    money: number;
}

export class StatusStatistic {
    activeAccounts: number = 0;
    deactiveAccounts: number = 0;
    totalAccounts: number = 0;
}


