import {Component, OnInit, ViewEncapsulation} from "@angular/core";
import {Router} from "@angular/router-deprecated";
import { Observable } from 'rxjs/Observable';
import {Account} from '../account/account.model';
import { DOMHelper } from '../shared/helpers';
import { Patient, StatusStatistic } from "./patient.model";
import PatientService from "./patient.service";
import { patients as dumpPatients} from '../shared/dump.model';

@Component({
    selector: 'admin-patients',
    template: require('./patientmgt.component.html'),
    styles: [
        require('../../sass/layouts/patient-content.scss')
    ],
    encapsulation: ViewEncapsulation.None,
    providers: [PatientService]
})
export class PatientMgtComponent implements OnInit {
    private recordsPerPage: number = 20;
    private currentPage: number;
    private totalPages: number;
    private patients: Array<Patient> = [];
    private account: Account;
    private statusStat: StatusStatistic = new StatusStatistic();

    private tableDataLoading: boolean = false;
    private btnArrayDataLoading: Array<boolean> = [];

    constructor(private patientService: PatientService,
        private router: Router) { }

    ngOnInit() {
        // check account exists
        this.account = JSON.parse(window.sessionStorage.getItem('account'));
        if (!this.account) {
            window.location.href = "/signin";
        }

        this.getStatusStatistic();

        let i = 0;
        while (i < this.recordsPerPage) {
            this.btnArrayDataLoading[i] = false;
            i++;
        }

        // get initial data
        this.currentPage = 1;
        this.tableDataLoading = true;
        this.patientService.filterPatients(this.account, null, null, this.currentPage, this.recordsPerPage)
            .subscribe(
            success => {
                if (success.status == 1) {
                    this.patients = success.data;
                    this.totalPages = Math.ceil(parseInt(success.total) / this.recordsPerPage);
                } else {
                    console.log(success.errorMsg);
                }
                this.tableDataLoading = false;
            },
            error => {
                console.error('rERROR ' + error);
                this.tableDataLoading = false;
            }
            );
    }

    getStatusStatistic() {
        this.patientService.getDoctorStatusStatistic(this.account)
            .subscribe(
            success => {
                if (success.status == 1)
                    this.statusStat = success.data;
                else
                    console.log(success.errorMsg);
            },
            error => console.log('rERROR: ' + error)
            );
    }

    searchRecords() {
        // get values
        let emailElem = <HTMLInputElement>document.getElementById('s-email');
        let email: string = emailElem.value;
        var statusElem: HTMLSelectElement = <HTMLSelectElement>document.getElementById("s-status");
        let statusOpt = <HTMLOptionElement>statusElem.options[statusElem.selectedIndex];
        let status: string = statusOpt.value;

        // request server
        this.tableDataLoading = true;
        this.currentPage = 1;
        this.patientService.filterPatients(this.account, email, status, this.currentPage, this.recordsPerPage)
            .subscribe(
            success => {
                if (success.status == 1) {
                    this.patients = success.data;
                    this.totalPages = Math.ceil(parseInt(success.total) / this.recordsPerPage);
                } else {
                    console.log(success.errorMsg);
                }
                this.tableDataLoading = false;
            },
            error => {
                console.error('rERROR:' + error);
                this.tableDataLoading = false;
            }
            );
    }

    goToPage(pageNo: number) {
        if (pageNo <= 0 || pageNo > this.totalPages) {
            return false;
        }
        // get values
        let emailElem = <HTMLInputElement>document.getElementById('s-email');
        let email: string = emailElem.value;
        var statusElem: HTMLSelectElement = <HTMLSelectElement>document.getElementById("s-status");
        let statusOpt = <HTMLOptionElement>statusElem.options[statusElem.selectedIndex];
        let status: string = statusOpt.value;

        // sending request
        DOMHelper.scrollUpTo(document.body, 0, 600);
        this.tableDataLoading = true;
        this.patientService.filterPatients(this.account, email, status, pageNo, this.recordsPerPage)
            .subscribe(
            success => {
                if (success.status == 1) {
                    this.patients = success.data;
                    this.totalPages = Math.ceil(parseInt(success.total) / this.recordsPerPage);
                    this.currentPage = pageNo;
                } else {
                    console.log(success.errorMsg);
                }
                this.tableDataLoading = false;
            },
            error => {
                console.error('rERROR:' + error);
                this.tableDataLoading = false;
            }
            );
        return false;
    }

    onSelectPatient(patient: Patient) {
        this.router.navigate(['PatientDetails', { id: patient.id }]);
    }

    // Show status seleciton dialog
    showStatusSelectionModal(index: number) {
        let patient = this.patients[index];
        let modalElem = <HTMLDivElement>document.getElementById('modal-change-status');
        window.addEventListener('click', this.windowClickEventHandler, false);
        document.getElementById('modal-status-title').textContent = `Change status of doctor ${patient.firstName} ${patient.lastName} ?`;
        if (patient.status == 0) {
            let deactiveStatusElem = <HTMLInputElement>document.getElementById('deactive-radio');
            deactiveStatusElem.checked = true;
        } else {
            let activeStatusElem = <HTMLInputElement>document.getElementById('active-radio');
            activeStatusElem.checked = true;
        }
        if (!modalElem.classList.contains('modal-open')) {
            modalElem.classList.add('modal-open');
            modalElem.classList.remove('modal-close');
        }

        modalElem.setAttribute('data-patient-index', index + '');
    }


    hideStatusSelectionModal() {
        let modalElem = <HTMLDivElement>document.getElementById('modal-change-status');
        if (modalElem.classList.contains('modal-open')) {
            modalElem.classList.remove('modal-open');
            modalElem.classList.add('modal-close');
        }
        window.removeEventListener('click', this.windowClickEventHandler, false);
    }

    windowClickEventHandler(event: any) {
        let modalStatusContentElem = <HTMLDivElement>document.getElementById('modal-chst-overlay');
        if (event.target == modalStatusContentElem) {
            let modalElem = <HTMLDivElement>document.getElementById('modal-change-status');
            if (modalElem.classList.contains('modal-open')) {
                modalElem.classList.remove('modal-open');
                modalElem.classList.add('modal-close');
            }
            window.removeEventListener('click', this.windowClickEventHandler, false);
        }
    }

    saveStatusSelection() {
        let modalElem = <HTMLDivElement>document.getElementById('modal-change-status');
        const index: number = parseInt(modalElem.getAttribute('data-patient-index'));

        // Get selected value
        let status: number = this.patients[index].status;
        let deactiveRadioElem = <HTMLInputElement>document.getElementById('deactive-radio');
        let activeRadioElem = <HTMLInputElement>document.getElementById('active-radio');
        if (deactiveRadioElem.checked) {
            status = parseInt(deactiveRadioElem.value);
        } else if (activeRadioElem.checked) {
            status = parseInt(activeRadioElem.value);
        }

        // Set button to loading state

        // Send request update status
        if ((status == 0 || status == 1 || status == 2) && this.patients[index].status != status) {
            this.btnArrayDataLoading[index] = true;
            this.patientService.changeStatus(this.account, this.patients[index].id, status)
                .subscribe(
                success => {
                    if (success.status == 1) {
                        this.patients[index].status = status;
                        this.getStatusStatistic();
                    } else {
                        console.log(success.errorMsg);
                    }
                    this.btnArrayDataLoading[index] = false;
                },
                error => {
                    console.error('rERROR' + error);
                    this.btnArrayDataLoading[index] = false;
                }
                );
        }

        this.hideStatusSelectionModal();
    }

    cancelStatusSelectionModal() {
        this.hideStatusSelectionModal();
    }

    diagnostic() {
        return JSON.stringify(this.patients);
    }
} 



