import {Component, OnInit} from "@angular/core";
import { RouteConfig, ROUTER_DIRECTIVES, ROUTER_PROVIDERS, RouterOutlet, Router } from '@angular/router-deprecated';
import {DashboardComponent} from "../dashboard/dashboard.component";
import {PatientMgtComponent} from "../patient/patientmgt.component";
import {PatientDetailsComponent} from "../patient/patient-details.component";
import {DoctorMgtComponent} from "../doctor/doctormgt.component";
import {DoctorDetailsComponent} from "../doctor/doctor-details.component";
import {Account} from "./../account/account.model";

@Component({
  selector: 'admin',
  template: require('./admin.component.html'),
  styles: [
    require('../../sass/pages/admin.scss')
  ],
  directives: [ROUTER_DIRECTIVES]
})
@RouteConfig([
  { path: '/', name: 'Dashboard', component: DashboardComponent, useAsDefault: true },
  { path: '/patients', name: 'PatientMgt', component: PatientMgtComponent },
  { path: '/patients/:id', name: 'PatientDetails', component: PatientDetailsComponent },
  { path: '/doctors', name: 'DoctorMgt', component: DoctorMgtComponent },
  { path: '/doctors/:id', name: 'DoctorDetails', component: DoctorDetailsComponent }
])
export class AdminComponent implements OnInit {
  private account: Account;
  constructor(private router: Router) { }

  ngOnInit() {
    this.account = JSON.parse(window.sessionStorage.getItem('account'));
    if (!this.account) {
      this.router.navigate(['Signin']);
    }

  }

  // Show account dropdown
  onToggleSubNameNav() {
    let navAccountDropDownElem = <HTMLDivElement>document.getElementById('nav-account-dropdown');
    navAccountDropDownElem.classList.toggle('show');
    return false;
  }
  
  signOut() {
    this.account = null;
    window.sessionStorage.setItem('account', null);
    window.location.href = "/";
    return false;
  }

  // Get current section that is presenting -> crap!
  currentSection() {
    let pathArr = window.location.pathname.split('/');
    if (pathArr[1] == 'admin' && pathArr.length >= 2) {
      if (pathArr.length == 2 || pathArr[2] == 'dashboard') {
        return 'dashboard';
      } else if (pathArr[2] == 'patients') {
        return 'patients';
      } else if (pathArr[2] == 'doctors') {
        return 'doctors';
      }
    }
    return "none";
  }
} 



