import { Component, ViewEncapsulation, OnInit } from '@angular/core';
import { RouteConfig, ROUTER_DIRECTIVES, ROUTER_PROVIDERS, Router } from '@angular/router-deprecated';
import { HTTP_PROVIDERS } from '@angular/http';
import {SigninComponent} from './account/signin.component';
import {AdminComponent} from './admin/admin.component';
import {Account} from "./account/account.model";
import {account as dumpAcc} from './shared/dump.model';

@Component({
    selector: 'my-app',
    template: require('./app.component.html'),
    styles: [
      require('../sass/main.scss')
    ],
    encapsulation: ViewEncapsulation.None,
    directives: [ROUTER_DIRECTIVES],
    providers: [ROUTER_PROVIDERS, HTTP_PROVIDERS]
})
@RouteConfig([
    {
        path: '/signin', name: 'Signin', component: SigninComponent, useAsDefault: true
    },
    {
        path: '/admin/...', name: 'Admin', component: AdminComponent
    },
])
export class AppComponent implements OnInit{ 
    private account: Account;
    
    constructor(private router: Router) {}
    
    ngOnInit() {
        //window.sessionStorage.setItem('account', JSON.stringify(dumpAcc));
        this.account = JSON.parse(window.sessionStorage.getItem('account'));
        if (!this.account) {
            this.router.navigate(['Signin']);
        }
    }
    
}



