import {Component, OnInit} from "@angular/core";
import {NgForm} from "@angular/common";
import {Router} from "@angular/router-deprecated";
import "../shared/rxjs-operators";
import messages from "../shared/messages";
import Logger from "../shared/logger.service";
import {Account} from "./account.model";
import {SigninModel} from "./signin.model";
import SigninService from "./signin.service";

@Component({
   selector: 'login',
   template: require('./signin.component.html'),
   styles: [
      require('../../sass/pages/signin.scss')
   ],
   providers: [SigninService]
})
export class SigninComponent{
    private model: SigninModel;
    private isInvalid: boolean;
    private logger: Logger;

    private signinDataLoading = false;
    
    constructor(private router: Router, private signinService: SigninService) {
    }
    
    ngOnInit() {
        if (JSON.parse(window.sessionStorage.getItem('account'))) {
            this.router.navigate(['Admin']);
        }
        this.model = new SigninModel();
        this.logger = new Logger("SigninComponent");
        this.isInvalid = false;
    }
    
    signIn() {
        this.logger.log("On submiting...");
        if (this.model.email == "") {
            this.logger.log("email")
            document.querySelector('#errormsg_email').textContent = messages.get('E001');
            this.isInvalid = true;
        }
        if (!this.model.password) {
            document.querySelector('#errormsg_pwd').textContent = messages.get('E002');
            this.isInvalid = true;
        } 
        if (!this.isInvalid) {
            this.signinDataLoading = true;
            this.signinService.signIn(this.model)
                            .subscribe(
                                success => {
                                    if (success.status == 1) {
                                        window.sessionStorage.setItem('account', JSON.stringify(success.data));
                                        this.router.navigate(['Admin']);
                                    } else {
                                        this.logger.log("Error when trying to sign in into server: " + success.errorMsg);
                                        document.querySelector('#errormsg_com>.errormsg').textContent = messages.get('E004');
                                    }
                                    this.signinDataLoading = false;
                                },
                                error => {
                                    this.logger.log("Error when trying to sign in into server: " + error);
                                    document.querySelector('#errormsg_com>.errormsg').textContent = messages.get('E004');
                                    this.isInvalid = true;
                                    this.signinDataLoading = false;
                                }
                            );
        }
    }
    
    get diagnostic(): string {
        return JSON.stringify(this.model);
    }
    
} 



